@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
.App {

}
.App-header{
  background: -webkit-linear-gradient(left, #ffedbc, #ED4264);
  background: linear-gradient(to right, #ffedbc, #ED4264);
  text-align: right;
  border-bottom: 1px solid #ED4264;
}
.App-logo {
  -webkit-animation: App-logo-spin 3s linear;
          animation: App-logo-spin 3s linear;
  height: 15vh;
  padding: 10px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg) scale(0.3);
            transform: rotate(0deg) scale(0.3);
  }
  to {
    -webkit-transform: rotate(360deg) scale(1);
            transform: rotate(360deg) scale(1);
  }

}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg) scale(0.3);
            transform: rotate(0deg) scale(0.3);
  }
  to {
    -webkit-transform: rotate(360deg) scale(1);
            transform: rotate(360deg) scale(1);
  }

}

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: -webkit-linear-gradient(left, #ffedbc, #ED4264);
  background: linear-gradient(to right, #ffedbc, #ED4264);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.main {
  background: #fff;
  box-shadow: 0px 10px 35px -15px black;
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
}

.title-container {
  height: 90vh;
  width: 40%;
  background: url(/react-weathercheck-practice/static/media/bg.b6796c73.jpg) center center no-repeat;
  background-size: cover;
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.title-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  background-color: #2c3e50;
  width: 60%;
  height: 90vh;
  padding-top: 100px;
  padding-left: 50px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #ED4264;
  width: 30%;
  padding-bottom: 4px;
  color: #fff;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] {
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #ED4264;
  color: #fff;
  font-weight: 100;
}

button:active {
  outline: none;
  text-decoration: none;
  border: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: #ED4264;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #ED4264;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}
@media screen and (max-width: 600px){
  .main{
    flex-direction: column;
    width: 100%;
  }
  .title-container{
    width: 100%;
  }
  .form-container{
    width: 100%;
  }
  .wrapper {
    height: 100%;
    padding-bottom: 30px;
  }

}

